.team-container{
    padding: 30px 40px;
    margin: 130px auto;
    width: 50%;
    box-shadow: 0 0 7px 0 rgba(0, 0, 0, 0.25);
    border-radius: 8px;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-items: flex-start;
    background: white;
}


.team-container h1{
    margin: 15px;
}

.team-container h3{
    margin: 10px;
}

.team-container ul{
    display: flex;
    width: 80%;
    justify-content: space-around;
    list-style-type: none;
}

.team-container li{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.team-container .img-container{
    height: 70px;
    width: 70px;
}

.team-container .img-container img{
    object-fit: cover;
    border-radius: 50%;
    width: 100%;
    height: 100%;
}
